export const VERT_ALIGN_TOGGLE_SUB = Symbol('VERT_ALIGN_TOGGLE_SUB');

export const VERT_ALIGN_TOGGLE_SUP = Symbol('VERT_ALIGN_TOGGLE_SUP');

export function toggleSub() {
  return {
    type: VERT_ALIGN_TOGGLE_SUB,
  };
}

export function toggleSup() {
  return {
    type: VERT_ALIGN_TOGGLE_SUP,
  };
}
